.inputPageContainer {
  color: #dfdfdf;
  background: #333; }
  .inputPageContainer .introContainer {
    padding-top: 50px;
    max-width: 1200px;
    margin: 0 auto; }
    .inputPageContainer .introContainer p {
      font-size: 20px;
      text-align: center; }
    .inputPageContainer .introContainer .imgContainer {
      background-color: white;
      box-shadow: -5px -7px 18px -3px #888888; }
      .inputPageContainer .introContainer .imgContainer img {
        max-width: 100%;
        height: auto;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); }
      .inputPageContainer .introContainer .imgContainer .imgTextContainer {
        color: black;
        text-align: center;
        padding: 20px 10px; }
        .inputPageContainer .introContainer .imgContainer .imgTextContainer span {
          vertical-align: middle; }
  .inputPageContainer .homeTitle {
    text-align: center;
    margin-top: 50px; }
  .inputPageContainer .errorTitle {
    text-align: center;
    margin-top: 25px;
    color: red; }
  .inputPageContainer .inputContainer {
    width: 80%;
    margin: 0 auto; }
    .inputPageContainer .inputContainer label,
    .inputPageContainer .inputContainer #homeRadio {
      padding-top: 10px;
      padding-bottom: 10px; }
    .inputPageContainer .inputContainer select {
      margin-bottom: 10px; }
    .inputPageContainer .inputContainer .elBreak {
      margin-bottom: 30px; }
    .inputPageContainer .inputContainer #submitButton {
      margin: 5% 0;
      width: 100%; }

@media only screen and (min-width: 1024px) {
  .inputPageContainer {
    max-width: 1920px;
    margin: 0 auto; }
    .inputPageContainer .imgContainer {
      margin-bottom: 100px; }
    .inputPageContainer .homeTitle {
      margin-top: 150px; }
    .inputPageContainer .inputContainer {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-bottom: 150px; }
      .inputPageContainer .inputContainer .homeInnerContainer {
        display: grid;
        width: 100%;
        grid-template-columns: 10% 20% 8% 10% 36% 8% 10%;
        grid-template-rows: 100px 50px 50px 50px 50px auto;
        grid-template-areas: " . radio radio . . . ." ". label1 select1 . labelEL . ." ". label3 select3 . label2 select2 ." ". label5 select5 . label4 select4 ." ". label7 select7 . label6 select6 ." ". submitButton submitButton submitButton submitButton submitButton ."; }
        .inputPageContainer .inputContainer .homeInnerContainer #homeRadio {
          align-self: center;
          grid-area: radio; }
        .inputPageContainer .inputContainer .homeInnerContainer .labelEL {
          align-self: center;
          grid-area: labelEL; }
        .inputPageContainer .inputContainer .homeInnerContainer .label1 {
          align-self: center;
          grid-area: label1; }
        .inputPageContainer .inputContainer .homeInnerContainer .select1 {
          align-self: center;
          grid-area: select1; }
        .inputPageContainer .inputContainer .homeInnerContainer .label2 {
          align-self: center;
          grid-area: label2; }
        .inputPageContainer .inputContainer .homeInnerContainer .select2 {
          align-self: center;
          grid-area: select2; }
        .inputPageContainer .inputContainer .homeInnerContainer .label3 {
          align-self: center;
          grid-area: label3; }
        .inputPageContainer .inputContainer .homeInnerContainer .select3 {
          align-self: center;
          grid-area: select3; }
        .inputPageContainer .inputContainer .homeInnerContainer .label4 {
          align-self: center;
          grid-area: label4; }
        .inputPageContainer .inputContainer .homeInnerContainer .select4 {
          align-self: center;
          grid-area: select4; }
        .inputPageContainer .inputContainer .homeInnerContainer .label5 {
          align-self: center;
          grid-area: label5; }
        .inputPageContainer .inputContainer .homeInnerContainer .select5 {
          align-self: center;
          grid-area: select5; }
        .inputPageContainer .inputContainer .homeInnerContainer .label6 {
          align-self: center;
          grid-area: label6; }
        .inputPageContainer .inputContainer .homeInnerContainer .select6 {
          align-self: center;
          grid-area: select6; }
        .inputPageContainer .inputContainer .homeInnerContainer .label7 {
          align-self: center;
          grid-area: label7; }
        .inputPageContainer .inputContainer .homeInnerContainer .select7 {
          align-self: center;
          grid-area: select7; }
        .inputPageContainer .inputContainer .homeInnerContainer .submitButton {
          grid-area: submitButton; }
        .inputPageContainer .inputContainer .homeInnerContainer label {
          padding-top: 0px; }
        .inputPageContainer .inputContainer .homeInnerContainer select {
          margin-bottom: 0px; }
        .inputPageContainer .inputContainer .homeInnerContainer .elBreak {
          margin-left: 10px;
          margin-bottom: 0px; } }
