*, *::before, *::after {
  box-sizing: border-box; }

.modal-open {
  overflow: hidden; }

html {
  height: 100%; }
  html body {
    /* max-width: 1920px; */
    margin: 0px auto;
    min-height: 100%;
    padding: 0px;
    background: #333;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }
    html body #root {
      display: flex;
      flex-direction: column;
      height: 100vh; }
      html body #root .paper {
        background: #fff;
        color: #333;
        box-shadow: 0 -1px 1px rgba(0, 0, 0, 0.15), 0 -10px 0 -5px #eee, 0 -10px 1px -4px rgba(0, 0, 0, 0.15), 0 -20px 0 -10px #eee, 0 -20px 1px -9px rgba(0, 0, 0, 0.15);
        margin-top: 20px;
        margin-bottom: 20px; }

::-webkit-scrollbar {
  position: absolute;
  width: 7px; }

::-webkit-scrollbar:horizontal {
  background: #000;
  width: 7px;
  height: 7px; }

/* Track */
::-webkit-scrollbar-track {
  box-shadow: none;
  border-radius: 10px; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: #A9A9A9;
  border-radius: 10px; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: grey; }

.index-container {
  margin-top: 70px; }

.switch-wrapper44 {
  position: relative;
  flex: 1 0 auto; }

.switch-wrapper44 > div {
  position: absolute;
  width: 100%; }

.fade-enter {
  opacity: 0;
  z-index: 1; }

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-in; }
